.likes-page {
  min-height: 100vh;
  background: var(--bg-primary);
  color: var(--text-primary);
}

.likes-header {
  background: var(--bg-card);
  border-bottom: 2px solid var(--border-primary);
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  position: sticky;
  top: 0;
  z-index: 50;
}

.likes-header .back-btn {
  background: none;
  border: none;
  color: var(--accent-primary);
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  text-decoration: none;
  font-weight: 600;
}

.likes-header .back-btn:hover {
  background: var(--bg-elevated);
  color: var(--accent-secondary);
}

.likes-header .header-content {
  flex: 1;
}

.likes-header h1 {
  margin: 0;
  font-size: 2rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.likes-count {
  color: var(--text-secondary);
  font-size: 0.95rem;
  margin: 0.5rem 0 0 0;
}

.points-display {
  display: flex;
  align-items: center;
  background: var(--accent-gradient);
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-accent);
  transition: all 0.3s ease;
}

.points-display:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.points-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
}

.points-item .points-label {
  font-size: 0.75rem;
  color: var(--text-primary);
  opacity: 0.9;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 600;
}

.points-item .points-value {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

.likes-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.loading {
  text-align: center;
  padding: 3rem 2rem;
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #ef4444;
  padding: 1rem;
  border-radius: var(--radius-md);
  margin-bottom: 2rem;
  text-align: center;
}

.no-likes {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 2px dashed var(--border-primary);
}

.no-likes-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.no-likes h3 {
  font-size: 1.5rem;
  margin: 1rem 0;
  color: var(--text-primary);
}

.no-likes p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.no-likes .btn {
  display: inline-block;
  padding: 0.75rem 2rem;
  background: var(--accent-gradient);
  color: white;
  text-decoration: none;
  border-radius: var(--radius-md);
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.no-likes .btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
}

.likes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
}

.like-card {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  overflow: hidden;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.like-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.15);
}

.like-card .card-image {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
  background: var(--bg-elevated);
}

.like-card .card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.like-card:hover .card-image img {
  transform: scale(1.05);
}

.photo-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-tertiary) 100%);
}

.placeholder-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: 700;
}

.tier-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.like-card .card-content {
  padding: 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.like-card h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.3rem;
  color: var(--text-primary);
}

.like-card .location {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin: 0.25rem 0 0.75rem 0;
}

.like-card .bio {
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.5;
  margin: 0 0 1rem 0;
  flex: 1;
}

.like-card .interests {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.interest-tag {
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.35rem 0.75rem;
  border-radius: var(--radius-md);
  font-size: 0.8rem;
  border: 1px solid var(--border-primary);
}

.like-card .card-actions {
  padding: 0 1.5rem 1.5rem 1.5rem;
  display: flex;
  gap: 0.75rem;
}

.btn-unlike {
  flex: 1;
  padding: 0.75rem 1rem;
  background: var(--bg-elevated);
  color: var(--text-primary);
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.btn-unlike:hover {
  background: rgba(239, 68, 68, 0.1);
  border-color: #ef4444;
  color: #ef4444;
}

.btn-unlike:active {
  transform: scale(0.98);
}

.btn-message {
  flex: 1;
  padding: 0.75rem 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.btn-message:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.btn-message:active {
  transform: translateY(0);
}

/* Message Modal */
.message-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.message-modal {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  width: 90%;
  max-width: 500px;
  border: 1px solid var(--border-primary);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-primary);
}

.modal-header h3 {
  margin: 0;
  color: var(--text-primary);
  font-size: 1.2rem;
}

.modal-close {
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.modal-close:hover {
  color: var(--text-primary);
  transform: scale(1.1);
}

.modal-body {
  padding: 1.5rem;
}

.modal-body textarea {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  background: var(--bg-elevated);
  color: var(--text-primary);
  font-family: inherit;
  font-size: 0.95rem;
  resize: vertical;
  transition: all 0.3s ease;
}

.modal-body textarea:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(255, 107, 157, 0.1);
}

.modal-body textarea::placeholder {
  color: var(--text-muted);
}

.modal-actions {
  display: flex;
  gap: 1rem;
  padding: 1.5rem;
  border-top: 1px solid var(--border-primary);
}

.modal-actions .btn {
  flex: 1;
  padding: 0.75rem 1rem;
  font-size: 0.95rem;
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-secondary {
  background: var(--bg-elevated);
  color: var(--text-primary);
  border: 2px solid var(--border-primary);
}

.btn-secondary:hover {
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
}

/* Responsive */
@media (max-width: 768px) {
  .likes-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .likes-header h1 {
    font-size: 1.5rem;
  }

  .points-display {
    width: 100%;
    justify-content: space-around;
  }

  .likes-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.5rem;
  }

  .like-card .card-image {
    height: 200px;
  }

  .likes-content {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .likes-header {
    padding: 1rem;
  }

  .likes-header h1 {
    font-size: 1.3rem;
  }

  .points-display {
    flex-direction: column;
    gap: 0.75rem;
    width: 100%;
  }

  .points-divider {
    width: 100%;
    height: 1px;
  }

  .likes-grid {
    grid-template-columns: 1fr;
  }

  .like-card .card-image {
    height: 250px;
  }
}
