.browse-page {
  height: 100vh;
  max-height: 100vh;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.browse-header {
  background: var(--bg-card);
  padding: 1rem 2rem;
  box-shadow: var(--shadow-md);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-primary);
  backdrop-filter: blur(10px);
}

.header-title {
  text-align: center;
}

.header-title h1 {
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.header-motto {
  color: var(--text-secondary);
  font-size: 0.85rem;
  font-style: italic;
  margin: 0;
  opacity: 0.8;
  letter-spacing: 0.5px;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.points-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.75rem 1rem;
  background: var(--bg-elevated);
  border-radius: var(--radius-md);
  border: 2px solid var(--border-primary);
  transition: all 0.3s ease;
  min-width: 80px;
}

.points-display:hover {
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  transform: translateY(-1px);
}

.points-count {
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--text-primary);
  line-height: 1;
}

.points-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-top: 2px;
  font-weight: 500;
}

.browse-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(100vh - 120px);
  gap: 2rem;
}

.profiles-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 2rem;
}

.profile-card-container {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.profile-card {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  overflow: hidden;
  transition: all 0.3s ease;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  flex-shrink: 0;
}

.profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.profile-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 0;
  background: transparent;
  border-bottom: 1px solid var(--border-primary);
  margin-bottom: 0.5rem;
  position: relative;
  height: 50px;
}

.tier-badge {
  width: auto;
  height: auto;
  border-radius: 0;
  display: none;
  font-size: 1rem;
  color: white;
  font-weight: bold;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.tier-label {
  color: white;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.profile-photo {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
  background: var(--bg-elevated);
  flex-shrink: 0;
  border-radius: var(--radius-lg);
  margin: 0 auto 0.5rem auto;
  box-shadow: var(--shadow-md);
}

.photo-button {
  width: 100%;
  height: 100%;
  border: none;
  padding: 0;
  background: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.photo-button:hover {
  transform: scale(1.05);
}

.photo-button img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo-placeholder {
  width: 100%;
  height: 100%;
  background: var(--bg-elevated);
  display: flex;
  align-items: center;
  justify-content: center;
}

.placeholder-avatar {
  width: 80px;
  height: 80px;
  border-radius: var(--radius-full);
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-size: 2rem;
  font-weight: bold;
  box-shadow: var(--shadow-md);
}

.profile-info {
  padding: 0;
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.profile-basic h2 {
  color: var(--text-primary);
  font-size: 1.2rem;
  margin-bottom: 0.25rem;
  font-weight: 700;
}

.location {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.profile-bio {
  margin-bottom: 1.5rem;
}

.profile-bio p {
  color: var(--text-secondary);
  line-height: 1.4;
  font-size: 0.85rem;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.profile-interests h4 {
  color: var(--text-primary);
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.interests-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.interest-tag {
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.4rem 0.8rem;
  border-radius: var(--radius-full);
  font-size: 0.8rem;
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.interest-tag:hover {
  background: var(--accent-primary);
  color: var(--text-primary);
  border-color: var(--accent-primary);
}

.profile-actions {
  display: flex;
  gap: 1.5rem;
  padding: 1rem 0;
  background: transparent;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.action-btn {
  width: auto;
  height: 45px;
  border: none;
  border-radius: var(--radius-full);
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0 1rem;
  box-shadow: var(--shadow-md);
}

.pass-btn {
  background: linear-gradient(135deg, #ff6b6b, #ee5a52);
  color: var(--text-primary);
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.pass-btn:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

.like-btn {
  background: var(--accent-gradient);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
}

.like-btn:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.action-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.browse-stats {
  text-align: center;
  margin-top: 0.5rem;
  color: var(--text-muted);
  font-size: 0.85rem;
}

.no-more-profiles {
  text-align: center;
  color: var(--text-secondary);
  max-width: 380px;
  padding: 2rem;
}

.no-profiles-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.no-more-profiles h3 {
  color: var(--text-primary);
  font-size: 0.85rem;
  margin-bottom: 1rem;
}

.no-more-profiles p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  font-size: 0.8rem;
}

.suggestions {
  background: var(--bg-card);
  padding: 1.5rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  margin-bottom: 2rem;
  text-align: left;
}

.suggestions p {
  color: var(--text-primary);
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.suggestions ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.suggestions li {
  color: var(--text-secondary);
  padding: 0.25rem 0;
  position: relative;
  padding-left: 1.5rem;
}

.suggestions li::before {
  content: '';
  color: var(--accent-primary);
  position: absolute;
  left: 0;
  font-weight: bold;
}

.back-btn {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  border: 2px solid var(--border-primary);
  background: var(--bg-elevated);
}

.back-btn:hover {
  color: var(--text-primary);
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  transform: translateY(-1px);
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  color: var(--error);
  padding: 1rem;
  border-radius: var(--radius-md);
  border: 1px solid rgba(239, 68, 68, 0.2);
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 1024px) {
  .profiles-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}

@media (max-width: 768px) {
  .browse-content {
    padding: 1rem 0.5rem;
  }
  
  .profiles-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .profile-card-container {
    max-width: 100%;
  }
  
  .profile-card {
    max-height: calc(100vh - 160px);
  }
  
  .browse-header {
    padding: 1rem;
  }
  
  .header-title h1 {
    font-size: 1.5rem;
  }
  
  .points-display {
    min-width: 60px;
    padding: 0.5rem 0.75rem;
  }
  
  .points-count {
    font-size: 1rem;
  }
  
  .points-label {
    font-size: 0.7rem;
  }
  
  .profile-photo {
    height: 250px;
  }
  
  .profile-info {
    padding: 1rem;
  }
  
  .profile-actions {
    padding: 1rem;
    flex-direction: row;
  }
  
  .action-btn {
    padding: 0.75rem;
  }
  
  .no-more-profiles {
    padding: 1rem;
    max-width: 100%;
  }
}
