.matches-page {
  min-height: 100vh;
  background: var(--bg-primary);
  color: var(--text-primary);
}

.matches-header {
  background: var(--bg-card);
  border-bottom: 2px solid var(--border-primary);
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  position: sticky;
  top: 0;
  z-index: 50;
}

.matches-header .back-btn {
  background: none;
  border: none;
  color: var(--accent-primary);
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  text-decoration: none;
  font-weight: 600;
}

.matches-header .back-btn:hover {
  background: var(--bg-elevated);
  color: var(--accent-secondary);
}

.matches-header .header-content {
  flex: 1;
}

.matches-header h1 {
  margin: 0;
  font-size: 2rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.matches-count {
  color: var(--text-secondary);
  font-size: 0.95rem;
  margin: 0.5rem 0 0 0;
}

.matches-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.loading {
  text-align: center;
  padding: 3rem 2rem;
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #ef4444;
  padding: 1rem;
  border-radius: var(--radius-md);
  margin-bottom: 2rem;
  text-align: center;
}

.no-matches {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 2px dashed var(--border-primary);
}

.no-matches-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.no-matches h3 {
  font-size: 1.5rem;
  margin: 1rem 0;
  color: var(--text-primary);
}

.no-matches p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.no-matches .btn {
  display: inline-block;
  padding: 0.75rem 2rem;
  background: var(--accent-gradient);
  color: white;
  text-decoration: none;
  border-radius: var(--radius-md);
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.no-matches .btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
}

.matches-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
}

.match-card {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  overflow: hidden;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.match-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.15);
}

.match-card .card-image {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
  background: var(--bg-elevated);
}

.match-card .card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.match-card:hover .card-image img {
  transform: scale(1.05);
}

.photo-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-tertiary) 100%);
}

.placeholder-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  font-weight: 700;
}

.tier-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.match-indicator {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 107, 157, 0.9);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-full);
  font-weight: 600;
  font-size: 0.9rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.match-card .card-content {
  padding: 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.match-card h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.3rem;
  color: var(--text-primary);
}

.match-card .location {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin: 0.25rem 0 0.75rem 0;
}

.match-card .bio {
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.5;
  margin: 0 0 1rem 0;
  flex: 1;
}

.match-card .interests {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.interest-tag {
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.35rem 0.75rem;
  border-radius: var(--radius-md);
  font-size: 0.8rem;
  border: 1px solid var(--border-primary);
}

.match-card .card-actions {
  padding: 0 1.5rem 1.5rem 1.5rem;
  display: flex;
  gap: 0.75rem;
}

.btn-message {
  flex: 1;
  padding: 0.75rem 1rem;
  background: var(--accent-gradient);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.95rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-message:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
}

.btn-message:active {
  transform: scale(0.98);
}

.btn-unmatch {
  flex: 1;
  padding: 0.75rem 1rem;
  background: var(--bg-elevated);
  color: var(--text-primary);
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.btn-unmatch:hover {
  background: rgba(239, 68, 68, 0.1);
  border-color: #ef4444;
  color: #ef4444;
}

.btn-unmatch:active {
  transform: scale(0.98);
}

/* Responsive */
@media (max-width: 768px) {
  .matches-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .matches-header h1 {
    font-size: 1.5rem;
  }

  .matches-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.5rem;
  }

  .match-card .card-image {
    height: 200px;
  }

  .matches-content {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .matches-grid {
    grid-template-columns: 1fr;
  }

  .match-card .card-image {
    height: 250px;
  }
}
