.user-detail-view {
  min-height: 100vh;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
}

.detail-header {
  background: var(--bg-card);
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  border-bottom: 1px solid var(--border-primary);
  box-shadow: var(--shadow-md);
}

.back-btn {
  background: var(--bg-elevated);
  border: 2px solid var(--border-primary);
  color: var(--text-secondary);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.back-btn:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border-color: var(--border-secondary);
}

.detail-header h1 {
  flex: 1;
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0;
}

.tier-badge {
  background: var(--accent-gradient);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-full);
  font-weight: 600;
  font-size: 0.85rem;
  text-transform: uppercase;
}

.detail-content {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  overflow-y: auto;
}

.photo-gallery {
  position: relative;
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  overflow: hidden;
  box-shadow: var(--shadow-lg);
  height: fit-content;
  top: 0;
  sticky: top;
}

.photo-gallery img {
  width: 100%;
  height: 500px;
  object-fit: cover;
  display: block;
}

.photo-placeholder {
  width: 100%;
  height: 500px;
  background: var(--bg-elevated);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
  color: var(--text-secondary);
  font-weight: bold;
}

.photo-nav {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(0, 0, 0, 0.6);
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-full);
  color: white;
}

.photo-nav button {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  width: 32px;
  height: 32px;
  border-radius: var(--radius-full);
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
}

.photo-nav button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.3);
}

.photo-nav button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.photo-nav span {
  font-size: 0.9rem;
  min-width: 60px;
  text-align: center;
}

.profile-details {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  overflow-y: auto;
  padding-right: 1rem;
}

.profile-details::-webkit-scrollbar {
  width: 6px;
}

.profile-details::-webkit-scrollbar-track {
  background: var(--bg-elevated);
  border-radius: 10px;
}

.profile-details::-webkit-scrollbar-thumb {
  background: var(--border-primary);
  border-radius: 10px;
}

.profile-details::-webkit-scrollbar-thumb:hover {
  background: var(--border-secondary);
}

.basic-info h2 {
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0 0 0.5rem 0;
}

.location {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0;
}

.detail-section {
  background: var(--bg-card);
  padding: 1.5rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
}

.detail-section h3 {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 1rem 0;
  font-weight: 600;
}

.attributes-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.attr {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.attr span {
  color: var(--text-secondary);
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.attr {
  color: var(--text-primary);
  font-size: 1rem;
}

.bio-text {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
}

.interests-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.interest-tag {
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-full);
  font-size: 0.9rem;
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.interest-tag:hover {
  background: var(--accent-primary);
  color: white;
  border-color: var(--accent-primary);
}

.detail-actions {
  display: flex;
  gap: 1rem;
  padding: 2rem;
  background: var(--bg-card);
  border-top: 1px solid var(--border-primary);
  justify-content: center;
}

.btn {
  padding: 0.75rem 2rem;
  border: none;
  border-radius: var(--radius-lg);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-primary {
  background: var(--accent-gradient);
  color: white;
  box-shadow: var(--shadow-accent);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.btn-secondary {
  background: linear-gradient(135deg, #ff6b6b, #ee5a52);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.btn-secondary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.loading,
.error {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: var(--text-secondary);
  font-size: 1.2rem;
}

@media (max-width: 1024px) {
  .detail-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .photo-gallery {
    position: relative;
    top: auto;
  }
}

@media (max-width: 768px) {
  .detail-header {
    padding: 1rem;
    flex-wrap: wrap;
  }

  .detail-header h1 {
    font-size: 1.4rem;
  }

  .detail-content {
    padding: 1rem;
    gap: 1rem;
  }

  .photo-gallery img,
  .photo-placeholder {
    height: 300px;
  }

  .attributes-grid {
    grid-template-columns: 1fr;
  }

  .detail-actions {
    flex-direction: column;
    padding: 1rem;
  }

  .btn {
    width: 100%;
    justify-content: center;
  }
}
