.privacy-link-button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
  padding: 0;
  margin-top: 10px;
  transition: color 0.3s ease;
}

.privacy-link-button:hover {
  color: #0056b3;
}

.privacy-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.privacy-modal {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  max-width: 700px;
  width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s ease;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.privacy-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e0e0e0;
}

.privacy-header h2 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.privacy-close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease;
}

.privacy-close-btn:hover {
  color: #333;
}

.privacy-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.privacy-content section {
  margin-bottom: 20px;
}

.privacy-content h3 {
  font-size: 16px;
  color: #333;
  margin: 15px 0 10px 0;
  font-weight: 600;
}

.privacy-content p {
  font-size: 14px;
  color: #666;
  line-height: 1.6;
  margin: 0 0 10px 0;
}

.privacy-content ul {
  margin: 10px 0;
  padding-left: 20px;
}

.privacy-content li {
  font-size: 14px;
  color: #666;
  line-height: 1.6;
  margin-bottom: 8px;
}

.privacy-actions {
  padding: 15px 20px;
  border-top: 1px solid #e0e0e0;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.privacy-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.privacy-btn-close {
  background-color: #007bff;
  color: white;
}

.privacy-btn-close:hover {
  background-color: #0056b3;
}

/* Scrollbar styling */
.privacy-content::-webkit-scrollbar {
  width: 8px;
}

.privacy-content::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.privacy-content::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.privacy-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}
