.messages-page {
  min-height: 100vh;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
}

.messages-header {
  background: var(--bg-card);
  padding: 1rem 2rem;
  box-shadow: var(--shadow-md);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-primary);
  flex-shrink: 0;
}

.messages-header h1 {
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0;
}

.messages-content {
  display: grid;
  grid-template-columns: 400px 1fr;
  flex: 1;
  overflow: hidden;
}

.matches-list {
  background: var(--bg-card);
  border-right: 1px solid var(--border-primary);
  overflow-y: auto;
}

.matches-list h3 {
  padding: 2rem 2rem 1rem;
  color: var(--text-primary);
  font-size: 1.3rem;
  margin: 0;
}

.matches-grid {
  padding: 0 1rem;
}

.match-item {
  display: flex;
  align-items: center;
  padding: 1rem;
  cursor: pointer;
  border-radius: var(--radius-lg);
  margin-bottom: 0.5rem;
  transition: all 0.2s;
  position: relative;
}

.match-item:hover {
  background: var(--bg-elevated);
}

.match-item.unread {
  background: rgba(255, 107, 157, 0.1);
}

.match-avatar {
  margin-right: 1rem;
}

.avatar-placeholder {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-weight: bold;
  font-size: 1.2rem;
}

.match-info {
  flex-grow: 1;
}

.match-info h4 {
  margin: 0 0 0.25rem 0;
  color: var(--text-primary);
  font-size: 1.1rem;
}

.match-info p {
  margin: 0 0 0.25rem 0;
  color: var(--text-secondary);
  font-size: 0.9rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.match-info .time {
  font-size: 0.8rem;
  color: var(--text-muted);
}

.unread-indicator {
  width: 12px;
  height: 12px;
  background: var(--accent-primary);
  border-radius: 50%;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.no-matches {
  padding: 3rem 2rem;
  text-align: center;
  color: var(--text-secondary);
}

.no-matches-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.no-matches h3 {
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.no-matches p {
  margin-bottom: 2rem;
}

.chat-area {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-secondary);
}

.chat-placeholder {
  text-align: center;
  color: var(--text-secondary);
}

.chat-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.chat-placeholder h3 {
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.back-btn {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  border: 2px solid var(--border-primary);
  background: var(--bg-elevated);
}

.back-btn:hover {
  color: var(--text-primary);
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  transform: translateY(-1px);
}

@media (max-width: 768px) {
  .messages-content {
    grid-template-columns: 1fr;
  }
  
  .chat-area {
    display: none;
  }
}
