.home-page {
  width: 100%;
  background: var(--bg-primary);
  color: var(--text-primary);
}

/* Navigation */
.home-nav {
  background: var(--bg-card);
  border-bottom: 2px solid var(--border-primary);
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.age-warning-banner {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.1) 0%, rgba(239, 68, 68, 0.05) 100%);
  border-bottom: 2px solid rgba(239, 68, 68, 0.3);
  padding: 1rem 2rem;
  text-align: center;
  color: var(--error);
  font-weight: 600;
  font-size: 1rem;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.age-checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  background: var(--bg-elevated);
  padding: 0.5rem 0.75rem;
  border-radius: var(--radius-md);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
  user-select: none;
  font-size: 0.9rem;
}

.age-checkbox-label:hover {
  background: var(--bg-tertiary);
  border-color: var(--accent-primary);
}

.age-checkbox {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: var(--accent-primary);
}

.age-text {
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.nav-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
}

.nav-link:hover {
  color: var(--text-primary);
  background: var(--bg-elevated);
}

.nav-link-primary {
  background: var(--accent-gradient);
  color: white;
  -webkit-text-fill-color: white;
}

.nav-link-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
}

/* Hero Section */
.hero {
  position: relative;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-tertiary) 100%);
  padding: 2rem;
  background-image: url('https://images.unsplash.com/photo-1519046904884-53103b34b206?w=1200&h=600&fit=crop');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(10, 10, 10, 0.7) 0%, rgba(26, 26, 26, 0.6) 100%);
  z-index: 1;
}

.hero-background {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  background: radial-gradient(circle at 100% 50%, rgba(255, 107, 157, 0.1) 0%, transparent 70%);
  pointer-events: none;
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 600px;
  padding: 2rem;
  flex: 1;
}

.hero-content h1 {
  font-size: 3.5rem;
  font-weight: 800;
  margin: 0 0 1rem 0;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.2;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.hero-content p {
  font-size: 1.25rem;
  color: var(--text-secondary);
  margin: 0 0 2rem 0;
  line-height: 1.6;
  text-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
}

.hero-tagline {
  font-size: 1.4rem !important;
  color: var(--accent-primary) !important;
  font-weight: 600;
  margin: 1rem 0 2rem 0 !important;
  letter-spacing: 0.5px;
}

/* Hero Graphics */
.hero-graphics {
  display: none;
}

.journey-timeline {
  display: none;
}

.journey-step {
  display: none;
}

.step-icon {
  display: none;
}

.journey-connector {
  display: none;
}

.floating-hearts {
  display: none;
}

.heart {
  display: none;
}

.cta-button {
  display: inline-block;
  padding: 1rem 2.5rem;
  background: var(--accent-gradient);
  color: white;
  text-decoration: none;
  border-radius: var(--radius-lg);
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 107, 157, 0.3);
  border: none;
  cursor: pointer;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(255, 107, 157, 0.4);
}

/* Features Section */
.features {
  max-width: 1200px;
  margin: 0 auto;
  padding: 5rem 2rem;
  text-align: center;
}

.features h2 {
  font-size: 2.5rem;
  margin: 0 0 3rem 0;
  color: var(--text-primary);
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.15);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.feature-card h3 {
  font-size: 1.3rem;
  margin: 1rem 0;
  color: var(--text-primary);
}

.feature-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
}

/* Couples Gallery */
.couples-gallery {
  max-width: 1200px;
  margin: 0 auto;
  padding: 5rem 2rem;
  text-align: center;
}

.couples-gallery h2 {
  font-size: 2.5rem;
  margin: 0 0 0.5rem 0;
  color: var(--text-primary);
}

.gallery-subtitle {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin: 0 0 3rem 0;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.couple-card {
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
  text-align: center;
}

.couple-card:hover {
  transform: scale(1.05);
  border-color: var(--accent-primary);
  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.15);
}

.couple-avatar {
  width: 100px;
  height: 100px;
  margin: 0 auto 1rem;
  border-radius: 50%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: 700;
  color: white;
  box-shadow: 0 4px 12px rgba(255, 107, 157, 0.3);
}

.couple-names {
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0.5rem 0;
  color: var(--text-primary);
}

.couple-status {
  color: var(--accent-primary);
  font-size: 0.95rem;
  margin: 0;
}

/* CTA Section */
.cta-section {
  background: linear-gradient(135deg, var(--accent-gradient));
  color: white;
  padding: 4rem 2rem;
  text-align: center;
  margin: 3rem 0;
}

.cta-section h2 {
  font-size: 2.5rem;
  margin: 0 0 1rem 0;
}

.cta-section p {
  font-size: 1.2rem;
  margin: 0 0 2rem 0;
  opacity: 0.95;
}

.cta-button-large {
  display: inline-block;
  padding: 1.2rem 3rem;
  background: white;
  color: var(--accent-primary);
  text-decoration: none;
  border-radius: var(--radius-lg);
  font-weight: 700;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.cta-button-large:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

/* Footer */
.home-footer {
  background: var(--bg-card);
  border-top: 2px solid var(--border-primary);
  padding: 3rem 2rem 1rem;
  margin-top: 3rem;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

.footer-section h4 {
  color: var(--text-primary);
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
}

.footer-section p {
  color: var(--text-secondary);
  margin: 0;
  line-height: 1.6;
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section ul li {
  margin-bottom: 0.5rem;
}

.footer-section a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-section a:hover {
  color: var(--accent-primary);
}

.footer-bottom {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 2rem;
  border-top: 1px solid var(--border-primary);
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .features h2,
  .couples-gallery h2,
  .cta-section h2 {
    font-size: 2rem;
  }

  .nav-container {
    flex-direction: column;
    gap: 1rem;
  }

  .gallery-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}


/* Privacy Policy Link in Footer */
.footer-section .privacy-link-button {
  display: block;
  margin-bottom: 0.5rem;
  text-align: left;
}
