.dashboard {
  height: 100vh;
  max-height: 100vh;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.dashboard-header {
  background: var(--bg-card);
  padding: 0.75rem 1.5rem;
  box-shadow: var(--shadow-md);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-primary);
  backdrop-filter: blur(10px);
  flex-shrink: 0;
}

.header-left h1 {
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 1.6rem;
  font-weight: bold;
  margin: 0;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-full);
  overflow: hidden;
  border: 2px solid var(--accent-primary);
  transition: all 0.3s ease;
}

.user-avatar:hover {
  border-color: var(--accent-secondary);
  transform: scale(1.05);
}

.user-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-weight: bold;
  font-size: 1.2rem;
}

.user-name {
  font-weight: 600;
  color: var(--text-primary);
}

.header-actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
}

.header-actions .btn {
  padding: 10px 12px;
  min-height: auto;
  min-width: 100px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 1.2;
  white-space: nowrap;
}

.header-actions a:nth-child(7) {
  flex-basis: 100%;
}

.header-actions .btn:hover,
.header-actions .btn:active,
.header-actions .btn:focus {
  background: #ff6b9d !important;
  color: #0a0a0a !important;
  border-color: #ff6b9d !important;
}

.dashboard-main {
  padding: 0.5rem 1rem;
  width: 100%;
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  height: auto;
  max-height: calc(100vh - 100px);
}

.welcome-section {
  flex-shrink: 0;
  margin-bottom: 0.25rem;
}

.welcome-content {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 0.8rem;
  background: var(--bg-card);
  padding: 0.5rem 0.8rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: hidden;
  align-items: center;
}

.welcome-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--accent-gradient);
}

.welcome-avatar {
  width: 45px;
  height: 45px;
  border-radius: var(--radius-full);
  overflow: hidden;
  border: 3px solid var(--accent-primary);
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.welcome-avatar:hover {
  border-color: var(--accent-secondary);
  transform: scale(1.05);
}

.welcome-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.welcome-avatar-placeholder {
  width: 100%;
  height: 100%;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  font-weight: bold;
  font-size: 2rem;
}

.welcome-text-left {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.welcome-text-left h2 {
  font-size: 1.3rem;
  color: var(--text-primary);
  margin-bottom: 0.1rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-text-left .welcome-motto {
  font-size: 0.75rem;
  color: var(--text-secondary);
  margin: 0;
  font-style: italic;
}

.welcome-text-right {
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.welcome-text-right p {
  font-size: 0.8rem;
  color: var(--text-secondary);
  margin-bottom: 0.3rem;
}

.welcome-text {
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.welcome-text h2 {
  font-size: 1.8rem;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-text p {
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}

.add-photo-prompt {
  display: inline-block;
  color: var(--accent-primary);
  text-decoration: none;
  font-weight: 600;
  padding: 0.5rem 1rem;
  border: 2px solid var(--accent-primary);
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  background: transparent;
  text-align: center;
  margin-left: auto;
}

.add-photo-prompt:hover {
  background: var(--accent-primary);
  color: var(--text-inverse);
  transform: translateY(-2px);
  box-shadow: var(--shadow-accent);
}

.discover-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: auto;
  height: auto;
}

.content-row {
  display: flex;
  gap: 0.8rem;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 0.5rem;
  height: auto;
  max-height: none;
}

.card-stack {
  position: relative;
  width: 260px;
  height: auto;
}

.profile-card {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  position: relative;
  width: 100%;
  height: auto !important;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
  padding: 0 !important;
}

.profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.6);
}

.card-image {
  height: 240px;
  background: var(--accent-gradient);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.card-image::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80px;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.3));
}

.placeholder-image {
  font-size: 0.9rem;
  color: var(--text-primary);
  opacity: 0.8;
}

.card-info {
  padding: 0;
  flex-grow: 0;
  display: none;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background: var(--bg-elevated);
  height: 0;
}

.card-info h3 {
  font-size: 1rem;
  margin-bottom: 0.05rem;
  color: var(--text-primary);
}

.card-info p {
  color: var(--text-secondary);
  font-size: 0.8rem;
  margin: 0;
}

.card-actions {
  padding: 0.75rem 0.5rem;
  display: flex;
  justify-content: center;
  gap: 1rem;
  background: var(--bg-elevated);
  align-items: center;
}

.action-counter {
  text-align: center;
  padding: 0.5rem;
  background: var(--bg-elevated);
  border-top: 1px solid var(--border-primary);
  font-size: 0.85rem;
  color: var(--text-secondary);
}

.action-counter p {
  margin: 0;
}

.btn-pass, .btn-like {
  width: 55px;
  height: 55px;
  border-radius: var(--radius-full);
  border: none;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  z-index: 10;
  flex-shrink: 0;
}

.btn-pass {
  background: linear-gradient(135deg, #ff6b6b, #ee5a52);
  color: var(--text-primary);
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.btn-like {
  background: var(--accent-gradient);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
}

.btn-pass:hover:not(:disabled), .btn-like:hover:not(:disabled) {
  transform: scale(1.1);
}

.btn-pass:hover:not(:disabled) {
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

.btn-like:hover:not(:disabled) {
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.btn-pass:disabled, .btn-like:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.quick-stats {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0.4rem;
  flex-shrink: 0;
  width: auto;
  margin-right: 0.5rem;
}

/* VIP Score Filter Dashboard */
.vip-score-filter-dashboard {
  background: linear-gradient(135deg, rgba(255, 107, 157, 0.15) 0%, rgba(255, 107, 157, 0.1) 100%);
  border: 2px solid rgba(255, 107, 157, 0.4);
  border-radius: var(--radius-lg);
  padding: 0.8rem;
  flex-shrink: 0;
  width: auto;
  min-width: 280px;
}

.vip-score-filter-dashboard h3 {
  margin: 0 0 0.3rem 0;
  font-size: 0.9rem;
  font-weight: 600;
  color: #1a1a1a;
}

.filter-subtitle {
  margin: 0 0 0.6rem 0;
  font-size: 0.85rem;
  color: #ff6b9d;
  font-style: italic;
}

.filter-controls {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}

.filter-inputs-row {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  justify-content: center;
}

.filter-input-group {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}

.filter-input-group label {
  font-size: 0.75rem;
  font-weight: 600;
  color: white;
  min-width: 30px;
}

.number-input {
  width: 50px;
  padding: 0.4rem;
  border: 1px solid rgba(255, 107, 157, 0.4);
  border-radius: 4px;
  font-size: 0.85rem;
  font-weight: 600;
  color: #333;
  background: white;
  text-align: center;
}

.number-input:focus {
  outline: none;
  border-color: #ff6b9d;
  box-shadow: 0 0 0 2px rgba(255, 107, 157, 0.15);
}

.percent {
  font-size: 0.75rem;
  font-weight: 600;
  color: white;
}

.vip-score-filter-dashboard .btn {
  padding: 0.6rem 1rem;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff5a8a 100%);
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.85rem;
  box-shadow: 0 3px 10px rgba(255, 107, 157, 0.3);
}

.vip-score-filter-dashboard .btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 14px rgba(255, 107, 157, 0.4);
}

.vip-score-filter-dashboard .btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.filter-message {
  font-size: 0.75rem;
  text-align: center;
  margin: 0.4rem 0 0 0;
  padding: 0.4rem;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.5);
  color: #333;
  font-weight: 500;
}

.stat-card {
  background: var(--bg-card);
  padding: 0.3rem 0.4rem;
  border-radius: var(--radius-lg);
  text-align: left;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
  position: relative;
  overflow: visible;
  min-width: 145px;
  width: 145px;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  padding-right: 0.6rem;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-gradient);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-primary);
}

.stat-card h3 {
  font-size: 1.1rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0.3rem 0 0 0;
  font-weight: 800;
  flex-shrink: 0;
}

.stat-card p {
  color: var(--text-secondary);
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  font-weight: 600;
  margin: 0;
  white-space: nowrap;
}

.questionnaire-card {
  text-decoration: none;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .dashboard-header {
    padding: 1rem;
  }
  
  .header-right {
    flex-direction: column;
    gap: 1rem;
  }
  
  .welcome-content {
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
  }
  
  .welcome-text h2 {
    font-size: 2rem;
  }
  
  .card-stack {
    width: 300px;
    height: auto;
  }
  
  .dashboard-main {
    padding: 1rem;
  }
}

/* Motto styling */
.header-motto {
  color: var(--text-secondary);
  font-size: 0.85rem;
  font-style: italic;
  margin: 0;
  opacity: 0.8;
  letter-spacing: 0.5px;
}

.welcome-motto {
  color: var(--accent-primary);
  font-size: 0.9rem;
  font-style: italic;
  font-weight: 500;
  margin: 5px 0 15px 0;
  letter-spacing: 0.5px;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
