.membership-page {
  min-height: 100vh;
  background: var(--bg-primary);
}

.membership-header {
  background: var(--bg-card);
  padding: 1rem 2rem;
  box-shadow: var(--shadow-md);
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  gap: 1rem;
  border-bottom: 1px solid var(--border-primary);
  backdrop-filter: blur(10px);
}

.membership-header .back-btn {
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 500;
  border-radius: var(--radius-sm);
  background: var(--bg-elevated);
  color: var(--text-secondary);
  border: 1px solid var(--border-primary);
  text-decoration: none;
  justify-self: start;
  min-width: 50px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.membership-header .back-btn:hover {
  background: var(--accent-primary);
  color: var(--text-primary);
  border-color: var(--accent-primary);
  text-decoration: none;
  transform: translateX(-2px);
  box-shadow: var(--shadow-accent);
}

.membership-header h1 {
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
  justify-self: center;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.membership-content {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.loading {
  text-align: center;
  padding: 3rem;
  font-size: 1.2rem;
  color: var(--text-secondary);
}

.error-message {
  color: var(--error);
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: var(--radius-md);
  font-size: 14px;
  text-align: center;
  backdrop-filter: blur(10px);
}

.current-membership {
  margin-bottom: 3rem;
}

.membership-card {
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  text-align: center;
  position: relative;
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.membership-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--accent-gradient);
  border-radius: var(--radius-xl) var(--radius-xl) 0 0;
}

.membership-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg);
}

.tier-badge {
  width: 60px;
  height: 60px;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  margin: 0 auto 1rem;
  background: var(--accent-gradient);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
  transition: all 0.3s ease;
}

.membership-card:hover .tier-badge {
  transform: scale(1.1);
}

.points-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0;
}

.points-count {
  font-size: 2rem;
  font-weight: bold;
  color: var(--text-primary);
}

.points-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.subscription-end {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background: var(--bg-elevated);
  border-radius: var(--radius-md);
  display: inline-block;
}

.tiers-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.tier-card {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  padding: 2rem;
  transition: all 0.3s ease;
  position: relative;
  border: 1px solid var(--border-primary);
  overflow: hidden;
}

.tier-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-gradient);
}

.tier-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.6);
  border-color: var(--accent-primary);
}

.tier-card.current-tier {
  border: 2px solid var(--accent-primary);
  transform: scale(1.02);
  box-shadow: var(--shadow-accent);
}

.tier-card.current-tier::before {
  height: 4px;
  background: var(--accent-gradient);
}

.tier-header {
  text-align: center;
  margin-bottom: 2rem;
}

.tier-icon {
  width: 50px;
  height: 50px;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  margin: 0 auto 1rem;
  background: var(--accent-gradient);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
  transition: all 0.3s ease;
}

.tier-card:hover .tier-icon {
  transform: scale(1.1) rotate(5deg);
}

.tier-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--text-primary);
  margin-top: 0.5rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.tier-features {
  margin-bottom: 2rem;
}

.tier-features h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-weight: 600;
}

.tier-features ul {
  list-style: none;
  padding: 0;
}

.tier-features li {
  padding: 0.5rem 0;
  color: var(--text-secondary);
  position: relative;
  padding-left: 1.5rem;
  transition: all 0.3s ease;
}

.tier-features li:before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--success);
  font-weight: bold;
}

.tier-features li:hover {
  color: var(--text-primary);
  transform: translateX(5px);
}

.point-costs {
  background: var(--bg-elevated);
  padding: 1.5rem;
  border-radius: var(--radius-md);
  margin-bottom: 2rem;
  border: 1px solid var(--border-primary);
}

.point-costs h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 600;
}

.costs-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.costs-grid div {
  padding: 0.5rem;
  background: var(--bg-card);
  border-radius: var(--radius-sm);
  text-align: center;
  font-size: 0.9rem;
  color: var(--text-secondary);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.costs-grid div:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.daily-points {
  text-align: center;
  font-weight: bold;
  color: var(--success);
  background: var(--bg-card);
  padding: 0.5rem;
  border-radius: var(--radius-sm);
  border: 1px solid var(--border-primary);
}

.upgrade-btn {
  width: 100%;
  padding: 15px;
  background: var(--accent-gradient);
  color: var(--text-primary);
  border: none;
  border-radius: var(--radius-md);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-accent);
  position: relative;
  overflow: hidden;
}

.upgrade-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.upgrade-btn:hover::before {
  left: 100%;
}

.upgrade-btn:hover:not(:disabled) {
  background: var(--accent-gradient-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.upgrade-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.current-plan-badge {
  background: var(--success);
  color: var(--text-primary);
  padding: 10px;
  border-radius: var(--radius-md);
  text-align: center;
  font-weight: bold;
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.points-info {
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.points-info:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.points-info h3 {
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.earning-methods {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.earning-item {
  background: var(--bg-elevated);
  padding: 1rem;
  border-radius: var(--radius-md);
  text-align: center;
  font-weight: 500;
  color: var(--text-secondary);
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.earning-item:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  transform: translateY(-2px);
  border-color: var(--accent-primary);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .membership-header {
    grid-template-columns: auto 1fr auto;
    gap: 1rem;
    padding: 1rem;
  }
  
  .membership-content {
    padding: 1rem;
  }
  
  .tiers-grid {
    grid-template-columns: 1fr;
  }
  
  .costs-grid {
    grid-template-columns: 1fr;
  }
  
  .earning-methods {
    grid-template-columns: 1fr;
  }
  
  .tier-card {
    padding: 1.5rem;
  }
}
/* Header motto styling */
.header-title {
  text-align: center;
}

.header-title h1 {
  margin-bottom: 5px;
}

.header-motto {
  color: var(--text-secondary);
  font-size: 0.85rem;
  font-style: italic;
  margin: 0;
  opacity: 0.8;
  letter-spacing: 0.5px;
}
/* Update header layout */
.membership-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: var(--bg-card);
  border-bottom: 1px solid var(--border-primary);
  box-shadow: var(--shadow-md);
}
/* Page layout fixes */
.membership-page {
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.membership-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
}