.auth-container {
  height: 100vh;
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-primary);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.auth-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(255, 107, 157, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(255, 107, 157, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.auth-card {
  background: var(--bg-card);
  padding: 40px;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  width: 100%;
  max-width: 400px;
  text-align: center;
  border: 1px solid var(--border-primary);
  position: relative;
  backdrop-filter: blur(10px);
}

.auth-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--accent-gradient);
  border-radius: var(--radius-xl) var(--radius-xl) 0 0;
}

.auth-card h1 {
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-weight: bold;
}

.auth-card .motto {
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-style: italic;
  margin-bottom: 20px;
  opacity: 0.8;
  letter-spacing: 0.5px;
}

.auth-card h2 {
  color: var(--text-primary);
  margin-bottom: 30px;
  font-weight: 300;
}

.form-group {
  margin-bottom: 20px;
}

.form-group input {
  width: 100%;
  padding: 15px;
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  font-size: 16px;
  transition: all 0.3s ease;
  background: var(--bg-elevated);
  color: var(--text-primary);
  box-sizing: border-box;
}

.password-group {
  position: relative;
}

.password-group .password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-group input {
  width: 100%;
  padding-right: 45px;
}

.password-group .password-toggle {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  padding: 0;
  width: auto;
  box-shadow: none;
  transition: all 0.2s ease;
  color: var(--text-secondary);
}

.password-group .password-toggle:hover {
  transform: scale(1.1);
  background: none;
  box-shadow: none;
  color: var(--text-primary);
}

.password-group .password-toggle:active {
  transform: scale(0.95);
}

.form-group input:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(255, 107, 157, 0.1);
  background: var(--bg-tertiary);
}

.form-group input::placeholder {
  color: var(--text-muted);
}

.error-message {
  color: var(--error);
  margin-bottom: 20px;
  padding: 12px 16px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: var(--radius-md);
  font-size: 14px;
  backdrop-filter: blur(10px);
}

button {
  width: 100%;
  padding: 15px;
  background: var(--accent-gradient);
  color: var(--text-primary);
  border: none;
  border-radius: var(--radius-md);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-accent);
  position: relative;
  overflow: hidden;
}

button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

button:hover::before {
  left: 100%;
}

button:hover:not(:disabled) {
  background: var(--accent-gradient-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.auth-card p {
  margin-top: 20px;
  color: var(--text-secondary);
}

.auth-card a {
  color: var(--accent-primary);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.auth-card a:hover {
  color: var(--accent-secondary);
  text-decoration: underline;
}


/* Terms Agreement */
.terms-agreement {
  margin-bottom: 20px;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.terms-checkbox {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  cursor: pointer;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.terms-checkbox input[type="checkbox"] {
  width: auto;
  min-width: 20px;
  height: 20px;
  margin-top: 2px;
  cursor: pointer;
  accent-color: var(--accent-primary);
}

.terms-checkbox span {
  line-height: 1.4;
}

.terms-agreement .terms-link-button {
  display: block;
  margin: 0 auto;
  padding: 0;
  font-size: 0.9rem;
  text-align: center;
}


/* Password Reset Modal */
.password-reset-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.password-reset-modal {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  max-width: 450px;
  width: 90%;
  animation: slideIn 0.3s ease;
  border: 1px solid var(--border-primary);
}

.password-reset-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid var(--border-primary);
}

.password-reset-header h2 {
  margin: 0;
  font-size: 20px;
  color: var(--text-primary);
}

.password-reset-close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: var(--text-secondary);
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease;
}

.password-reset-close:hover {
  color: var(--text-primary);
}

.password-reset-content {
  padding: 30px;
}

.reset-instruction {
  color: var(--text-secondary);
  font-size: 14px;
  margin-bottom: 20px;
  line-height: 1.5;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input-wrapper input {
  width: 100%;
  padding-right: 45px;
}

.password-input-wrapper .password-toggle {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  padding: 0;
  width: auto;
  box-shadow: none;
  transition: all 0.2s ease;
  color: var(--text-secondary);
}

.password-input-wrapper .password-toggle:hover {
  transform: scale(1.1);
  color: var(--text-primary);
}

.success-message {
  color: #10b981;
  margin-bottom: 20px;
  padding: 12px 16px;
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: var(--radius-md);
  font-size: 14px;
  backdrop-filter: blur(10px);
}


/* Forgot Password Link */
.forgot-password-link {
  background: none;
  border: none;
  color: var(--accent-primary);
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  padding: 0;
  font-size: 14px;
  transition: all 0.3s ease;
  box-shadow: none;
  width: auto;
}

.forgot-password-link:hover {
  color: var(--accent-secondary);
  text-decoration: underline;
}
