.ad-banner {
  background: var(--bg-card);
  border-top: 1px solid var(--border-primary);
  padding: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 50;
}

.ad-banner-bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid var(--border-primary);
  border-bottom: none;
}

.ad-banner-top {
  position: sticky;
  top: 0;
  border-bottom: 1px solid var(--border-primary);
  border-top: none;
}

.ad-banner-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 728px;
  position: relative;
}

.ad-banner-slot {
  width: 100%;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ad-banner-close {
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: var(--text-secondary);
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
  transition: all 0.2s ease;
  z-index: 51;
}

.ad-banner-close:hover {
  color: var(--text-primary);
  transform: translateY(-50%) scale(1.2);
}

/* Ensure content doesn't overlap with bottom banner */
@media (max-width: 768px) {
  .ad-banner-bottom {
    padding-bottom: 0.5rem;
  }
}

/* AdMob responsive styles */
.ad-banner-slot ins {
  display: block !important;
  margin: 0 auto;
}

/* Hide ads on print */
@media print {
  .ad-banner {
    display: none;
  }
}
