.profile-page {
  height: 100vh;
  background: var(--bg-primary);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.profile-header {
  background: var(--bg-card);
  padding: 1rem 2rem;
  box-shadow: var(--shadow-md);
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--border-primary);
  flex-shrink: 0;
  gap: 2rem;
}

.profile-header h1 {
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.back-btn {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  padding: 8px 12px;
  border-radius: var(--radius-md);
  border: 2px solid var(--border-primary);
  background: var(--bg-elevated);
  transition: all 0.3s ease;
}

.back-btn:hover {
  color: var(--text-primary);
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  transform: translateY(-1px);
}

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: var(--radius-md);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-primary {
  background: var(--accent-gradient);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
  padding: 10px 2px;
  font-size: 14px;
  min-width: auto;
  width: auto;
}

.btn-primary:hover {
  background: var(--accent-gradient-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.profile-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.profile-layout {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 2rem;
  align-items: start;
}

.section {
  background: var(--bg-card);
  padding: 1.5rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-primary);
}

.section h2 {
  color: var(--text-primary);
  margin: 0 0 1rem 0;
  font-size: 1.2rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.photos-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.info-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.delete-all-btn {
  background: #ff4444;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
  width: 100%;
}

.delete-all-btn:hover {
  background: #ff2222;
}

.photo-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

.photo-slot {
  aspect-ratio: 1;
  border: 2px dashed var(--border-secondary);
  border-radius: var(--radius-lg);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  background: var(--bg-elevated);
}

.photo-slot:hover {
  border-color: var(--accent-primary);
  background: var(--bg-tertiary);
  transform: scale(1.02);
}

.photo-slot.main-photo {
  grid-column: 1 / -1;
  aspect-ratio: 16/9;
}

.placeholder {
  font-size: 2rem;
  color: var(--text-muted);
  margin-bottom: 0.5rem;
}

.photo-slot:hover .placeholder {
  color: var(--accent-primary);
  transform: scale(1.1);
}

.photo-upload-label {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: relative;
}

.photo-upload-label span {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.photo-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.photo-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--radius-md);
  display: block;
}

.photo-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  opacity: 0;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.photo-slot:hover .photo-overlay {
  background: rgba(0, 0, 0, 0.6);
  opacity: 1;
}

.photo-delete-btn {
  background: rgba(255, 50, 50, 0.9);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.photo-delete-btn:hover {
  background: rgba(255, 30, 30, 1);
  transform: scale(1.05);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.form-group input,
.form-group textarea,
.form-group select {
  padding: 12px 16px;
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  font-size: 16px;
  transition: all 0.3s ease;
  background: var(--bg-elevated);
  color: var(--text-primary);
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(255, 107, 157, 0.1);
  background: var(--bg-tertiary);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-muted);
}

.info-display {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.info-display p {
  color: var(--text-primary);
  margin: 0;
  padding: 0.5rem 0;
  font-size: 0.95rem;
}

.info-display strong {
  color: var(--text-secondary);
  font-weight: 600;
}

.checkbox-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: var(--radius-md);
  transition: all 0.2s ease;
  color: var(--text-primary);
}

.checkbox-label:hover {
  background: var(--bg-elevated);
}

.checkbox-label input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: var(--accent-primary);
  flex-shrink: 0;
}

.interests-display {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.interest-tag {
  background: var(--accent-gradient);
  color: var(--text-primary);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-full);
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-sm);
}

.interest-tag:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-accent);
}

.save-btn {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 1rem;
}

.error-message {
  background: #fee;
  border: 1px solid #fcc;
  color: #c33;
  padding: 1rem;
  border-radius: var(--radius-md);
  margin-bottom: 1rem;
  font-weight: 500;
}

.success-message {
  background: #efe;
  border: 1px solid #cfc;
  color: #3c3;
  padding: 1rem;
  border-radius: var(--radius-md);
  margin-bottom: 1rem;
  font-weight: 500;
}

@media (max-width: 768px) {
  .profile-content {
    padding: 1rem;
    gap: 1rem;
  }

  .profile-layout {
    grid-template-columns: 1fr;
  }

  .section {
    padding: 1rem;
  }

  .profile-header {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }

  .photo-grid {
    grid-template-columns: 1fr;
  }

  .photo-slot.main-photo {
    aspect-ratio: auto;
    min-height: 200px;
  }
}
