.compatibility-questionnaire {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
}

.questionnaire-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 600px;
  width: 100%;
  padding: 40px;
  animation: slideUp 0.5s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.questionnaire-header {
  text-align: center;
  margin-bottom: 40px;
}

.questionnaire-header h1 {
  font-size: 28px;
  font-weight: 700;
  color: #1a1a1a;
  margin: 0 0 10px 0;
}

.subtitle {
  font-size: 16px;
  color: #333;
  margin: 0 0 20px 0;
  font-weight: 500;
  line-height: 1.6;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.progress-bar {
  height: 6px;
  background: #e0e0e0;
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 10px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #667eea 0%, #764ba2 100%);
  transition: width 0.3s ease;
}

.progress-text {
  font-size: 12px;
  color: #999;
  margin: 0;
}

.questionnaire-content {
  margin-bottom: 30px;
}

.error-message {
  background: #fee;
  color: #c33;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-size: 14px;
}

.question-card {
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.question-card h2 {
  font-size: 20px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0 0 25px 0;
  line-height: 1.4;
}

.options-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  margin-bottom: 20px;
}

.options-grid.multi-select {
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
}

.option-button {
  padding: 14px 16px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  background: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  transition: all 0.2s ease;
  text-align: left;
  position: relative;
  user-select: none;
}

.option-button:hover:not(:disabled) {
  border-color: #667eea;
  background: #f8f9ff;
  cursor: pointer;
}

.option-button.selected {
  border-color: #667eea;
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  color: #667eea;
  font-weight: 600;
}

.option-button .checkbox {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 2px solid #667eea;
  border-radius: 4px;
  margin-right: 8px;
  background: white;
  color: #667eea;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}

.option-button.selected .checkbox {
  background: #667eea;
  color: white;
}

.option-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.selection-hint {
  font-size: 12px;
  color: #999;
  margin-top: 12px;
  text-align: center;
}

.text-input-container {
  margin-bottom: 20px;
}

.text-input {
  width: 100%;
  padding: 14px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
  font-family: inherit;
  resize: vertical;
  transition: border-color 0.2s ease;
}

.text-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.character-count {
  font-size: 12px;
  color: #999;
  margin-top: 8px;
  text-align: right;
}

.questionnaire-actions {
  display: flex;
  gap: 12px;
  justify-content: space-between;
}

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  flex: 1;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-secondary {
  background: #f0f0f0;
  color: #333;
  border: 1px solid #e0e0e0;
}

.btn-secondary:hover:not(:disabled) {
  background: #e8e8e8;
}

.btn-secondary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: 16px;
  color: #666;
}

.error {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: 16px;
  color: #c33;
}

@media (max-width: 600px) {
  .questionnaire-container {
    padding: 24px;
  }

  .questionnaire-header h1 {
    font-size: 22px;
  }

  .question-card h2 {
    font-size: 18px;
  }

  .options-grid.multi-select {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .questionnaire-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }
}
