.rewards-page {
  min-height: 100vh;
  background: var(--bg-primary);
}

.rewards-header {
  background: var(--bg-card);
  padding: 1rem 2rem;
  box-shadow: var(--shadow-md);
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 1rem;
  border-bottom: 1px solid var(--border-primary);
  backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.rewards-header .back-btn {
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 500;
  border-radius: var(--radius-sm);
  background: var(--bg-elevated);
  color: var(--text-secondary);
  border: 1px solid var(--border-primary);
  text-decoration: none;
  min-width: 50px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.rewards-header .back-btn:hover {
  background: var(--accent-primary);
  color: var(--text-primary);
  border-color: var(--accent-primary);
  text-decoration: none;
  transform: translateX(-2px);
  box-shadow: var(--shadow-accent);
}

.rewards-header h1 {
  color: var(--text-primary);
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.points-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--accent-gradient);
  color: var(--text-primary);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  min-width: 80px;
  box-shadow: var(--shadow-accent);
  transition: all 0.3s ease;
}

.points-display:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.points-count {
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--text-primary);
  line-height: 1;
}

.points-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  opacity: 0.9;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-top: 2px;
  font-weight: 500;
}

.rewards-content {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.loading {
  text-align: center;
  padding: 3rem;
  font-size: 1.2rem;
  color: var(--text-secondary);
}

.error-message {
  color: var(--error);
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: var(--radius-md);
  font-size: 14px;
  text-align: center;
  backdrop-filter: blur(10px);
}

.success-message {
  color: var(--success);
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: var(--radius-md);
  font-size: 14px;
  text-align: center;
  position: relative;
  backdrop-filter: blur(10px);
}

.success-message button {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: var(--success);
  transition: all 0.3s ease;
}

.success-message button:hover {
  transform: translateY(-50%) scale(1.2);
}

.daily-bonus-section {
  margin-bottom: 2rem;
}

.daily-bonus-section h2 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.weekly-bonus-section {
  margin-bottom: 2rem;
  display: block;
}

.weekly-bonus-section h2 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.reward-card.weekly-bonus {
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.1) 0%, rgba(255, 165, 0, 0.1) 100%);
  border: 2px solid rgba(255, 215, 0, 0.3);
  width: 100%;
  padding: 1rem;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.reward-card.weekly-bonus:hover {
  border-color: rgba(255, 215, 0, 0.6);
  box-shadow: 0 8px 20px rgba(255, 215, 0, 0.2);
}

.claim-btn.weekly {
  background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
  color: #333;
  font-weight: 700;
  flex-shrink: 0;
  padding: 0.75rem 1.5rem;
  font-size: 0.95rem;
  width: fit-content;
}

.rewards-section {
  margin-bottom: 3rem;
}

.rewards-section h2 {
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.rewards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.reward-card {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  padding: 1.5rem;
  box-shadow: var(--shadow-md);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  transition: all 0.3s ease;
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: visible;
}

.reward-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-gradient);
}

.reward-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-primary);
}

.reward-card.daily-bonus {
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-secondary) 100%);
  color: var(--text-primary);
  box-shadow: var(--shadow-accent);
  width: 100%;
  padding: 1rem;
}

.reward-card.daily-bonus::before {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
}

.reward-card.unavailable {
  opacity: 0.6;
  background: var(--bg-elevated);
}

.reward-card.unavailable:hover {
  transform: none;
}

.reward-icon {
  font-size: 2.5rem;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.reward-card:hover .reward-icon {
  transform: scale(1.1) rotate(5deg);
}

.reward-info {
  flex-grow: 0;
  min-width: 200px;
  text-align: center;
}

.reward-info h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
  color: var(--text-primary);
  font-weight: 700;
}

.reward-card.daily-bonus .reward-info h3 {
  color: var(--text-primary);
}

.reward-info p {
  margin: 0 0 0.5rem 0;
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.4;
}

.reward-card.daily-bonus .reward-info p {
  color: rgba(255, 255, 255, 0.9);
}

.reward-category {
  display: inline-block;
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.2rem 0.5rem;
  border-radius: var(--radius-full);
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.reward-meta {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
}

.reward-time {
  color: var(--text-muted);
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.reward-points {
  color: var(--success);
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.reward-card.daily-bonus .reward-time,
.reward-card.daily-bonus .reward-points {
  color: rgba(255, 255, 255, 0.9);
}

.claim-btn, .start-btn {
  padding: 0.75rem 1rem;
  background: var(--accent-gradient);
  color: var(--text-primary);
  border: none;
  border-radius: var(--radius-md);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 70px;
  box-shadow: var(--shadow-accent);
  position: relative;
  overflow: hidden;
  font-size: 0.9rem;
  white-space: nowrap;
  flex-shrink: 0;
}

.claim-btn::before, .start-btn::before {
  display: none;
}

.claim-btn:hover::before, .start-btn:hover::before {
  display: none;
}

.claim-btn:hover:not(:disabled), .start-btn:hover:not(:disabled) {
  background: var(--accent-gradient-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(255, 107, 157, 0.4);
}

.claim-btn:disabled, .start-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.daily-bonus .claim-btn {
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

.daily-bonus .claim-btn:hover:not(:disabled) {
  background: rgba(0, 0, 0, 0.3);
}

.info-section {
  background: var(--bg-card);
  border-radius: var(--radius-xl);
  padding: 2rem;
  box-shadow: var(--shadow-lg);
  margin-top: 3rem;
  border: 1px solid var(--border-primary);
  transition: all 0.3s ease;
}

.info-section:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.info-section h3 {
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
  font-size: 1.3rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
}

.info-item {
  text-align: center;
  padding: 1rem;
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  background: var(--bg-elevated);
  border: 1px solid var(--border-primary);
}

.info-item:hover {
  transform: translateY(-3px);
  background: var(--bg-tertiary);
  border-color: var(--accent-primary);
}

.info-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}

.info-item:hover .info-icon {
  transform: scale(1.2);
}

.info-item h4 {
  color: var(--text-primary);
  margin: 0.5rem 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.info-item p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .rewards-header {
    grid-template-columns: auto 1fr;
    gap: 1rem;
    padding: 1rem;
  }
  
  .points-display {
    grid-column: 1 / -1;
    margin-top: 1rem;
  }
  
  .rewards-content {
    padding: 1rem;
  }
  
  .rewards-grid {
    grid-template-columns: 1fr;
  }
  
  .reward-card {
    flex-direction: column;
    text-align: center;
    padding: 1.25rem;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
  }
}
/* Header motto styling */
.header-title {
  text-align: center;
}

.header-title h1 {
  margin-bottom: 5px;
}

.header-motto {
  color: var(--text-secondary);
  font-size: 0.85rem;
  font-style: italic;
  margin: 0;
  opacity: 0.8;
  letter-spacing: 0.5px;
}
/* Update header layout */
.rewards-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem 2rem;
  background: var(--bg-card);
  border-bottom: 1px solid var(--border-primary);
  box-shadow: var(--shadow-md);
  gap: 1rem;
}

.points-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 0.75rem;
  background: var(--bg-elevated);
  border-radius: var(--radius-md);
  border: 2px solid var(--border-primary);
  transition: all 0.3s ease;
  min-width: 70px;
  gap: 0.25rem;
}

.points-display.earned {
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.1) 0%, rgba(16, 185, 129, 0.05) 100%);
  border-color: rgba(16, 185, 129, 0.3);
}

.points-display:hover {
  background: var(--bg-tertiary);
  border-color: var(--border-secondary);
  transform: translateY(-1px);
}

.points-display.earned:hover {
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.2) 0%, rgba(16, 185, 129, 0.1) 100%);
  border-color: rgba(16, 185, 129, 0.5);
}

.skip-video-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 0.75rem;
  background: var(--bg-elevated);
  border: 2px solid var(--border-primary);
  border-radius: var(--radius-md);
  color: var(--text-secondary);
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  min-width: 70px;
  transition: all 0.3s ease;
  text-decoration: none;
}

.skip-video-btn:hover {
  background: var(--accent-primary);
  color: var(--text-primary);
  border-color: var(--accent-primary);
  transform: translateY(-1px);
  box-shadow: var(--shadow-accent);
}

.points-count {
  font-size: 1rem;
  font-weight: bold;
  color: var(--text-primary);
  line-height: 1;
}

.points-label {
  font-size: 0.65rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.3px;
  margin-top: 0px;
  font-weight: 500;
}
/* Page layout fixes */
.rewards-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.rewards-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  margin-top: 80px;
}

/* Stats Section */
.stats-section {
  background: var(--bg-card);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-lg);
  padding: 2rem;
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  box-shadow: var(--shadow-md);
}

.stat-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  text-align: center;
  background: transparent;
  border: none;
  padding: 0;
  min-height: auto;
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
}

.stat-icon {
  font-size: 2rem;
  flex-shrink: 0;
}

.stat-info {
  flex: 1;
  text-align: center;
}

.stat-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 500;
  white-space: nowrap;
  margin: 0;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent-primary);
  margin-top: 4px;
  white-space: nowrap;
  margin: 0;
}

.stat-card:hover {
  transform: translateY(-2px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-md);
}
.controls-section {
  background: var(--bg-card);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-lg);
  padding: 1.5rem;
  margin-bottom: 2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: center;
}

.filter-group,
.sort-group {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.filter-group label,
.sort-group label {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 0.95rem;
}

.filter-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 0.5rem 1rem;
  background: var(--bg-elevated);
  color: var(--text-secondary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-full);
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.filter-btn:hover {
  background: var(--bg-tertiary);
  border-color: var(--accent-primary);
  color: var(--text-primary);
}

.filter-btn.active {
  background: var(--accent-gradient);
  color: var(--text-primary);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-accent);
}

.sort-select {
  padding: 0.5rem 1rem;
  background: var(--bg-elevated);
  color: var(--text-primary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-md);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.sort-select:hover,
.sort-select:focus {
  border-color: var(--accent-primary);
  background: var(--bg-tertiary);
  outline: none;
}

/* Section Header */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.section-header h2 {
  color: var(--text-primary);
  font-size: 1.5rem;
  margin: 0;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-count {
  background: var(--bg-elevated);
  color: var(--text-secondary);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-full);
  font-size: 0.85rem;
  font-weight: 500;
}

/* Card Badges */
.card-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: var(--accent-gradient);
  color: var(--text-primary);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-full);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  z-index: 1;
}

.card-badge.survey {
  background: linear-gradient(135deg, #4A90E2 0%, #357ABD 100%);
}

.card-badge.offer {
  background: linear-gradient(135deg, #FF6B6B 0%, #FF8E53 100%);
}

/* Enhanced Reward Cards */
.reward-card {
  position: relative;
}

.video-ad-card,
.survey-card,
.offer-card {
  transition: all 0.3s ease;
}

.video-ad-card:hover,
.survey-card:hover,
.offer-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 30px rgba(255, 107, 157, 0.2);
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem 2rem;
  background: var(--bg-card);
  border: 2px dashed var(--border-primary);
  border-radius: var(--radius-lg);
  margin: 2rem 0;
}

.empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.empty-state h3 {
  color: var(--text-primary);
  font-size: 1.3rem;
  margin: 0 0 0.5rem 0;
}

.empty-state p {
  color: var(--text-secondary);
  margin: 0;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .controls-section {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .filter-group,
  .sort-group {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }

  .filter-buttons {
    width: 100%;
  }

  .sort-select {
    width: 100%;
  }

  .stats-section {
    grid-template-columns: 1fr;
  }

  .section-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}